import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { VhAlgorithm, VhQueryCafe } from 'ionic-vhframeworks';
import { VhType } from 'src/app/cafe/interface/vh-type';

@Component({
  selector: 'app-search-service',
  templateUrl: './search-service.component.html',
  styleUrls: ['./search-service.component.scss'],
})
export class SearchServiceComponent implements OnInit {
  @Input('showDrawerServiceList') showDrawerServiceList: boolean = true;
  @Output('closeSearchServiceList') closeSearchServiceList: EventEmitter<any> = new EventEmitter();

  public serviceList: any[] = [];
  public choiceService: any = {};
  public searchList: any[] = [];
  public list_menu: any = [];
  public listTabs: Array<any> = [{ _id: 'all', name: 'All', type: 1 }];
  public indexTab: number = 0;
  public editPrice: any = {};
  public tempVal: string = '';
  public showOptions: boolean = false;
  public VhType = VhType;



  constructor(
    public vhAlgorithm: VhAlgorithm,
    private vhQueryCafe: VhQueryCafe
  ) {}



  ngOnInit(): void {
    this.serviceList = this.vhQueryCafe
      .getlocalDetailProducts(VhType.SERVICE_SERVICETIME)
      .filter((item) => !item.delete_hidden)
      .map((e) => {
        return { ...e, ...e.units[0] };
      });
    this.serviceList = this.vhAlgorithm.sortVietnamesebyASC(
      this.serviceList,
      'name'
    );
    this.list_menu = this.vhQueryCafe.getlocalCategorys(VhType.SERVICE).map((e) => {
      e.status = this.serviceList.find((pro) => pro.id_category == e._id)
        ? true
        : false;
      return { ...e };
    });
    this.listTabs = [
      ...this.listTabs,
      ...this.list_menu.filter((cate) => cate.status),
    ];
    this.searchList = this.serviceList;
  }
  ngAfterViewInit() {
    document.getElementById('inputSearchService').focus();
  }

  myTrackByFunction(element: any, index: any) {
    return element._id ? element._id : index;
  }

  goBack() {
    this.closeSearchServiceList.emit(false);
  }

  searchService(value: string) {
    let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());
    if (value.length)
      this.searchList = this.vhAlgorithm.searchList(tempVal, this.serviceList, [
        'name',
      ]);
    else this.searchList = this.serviceList;
  }

  selectService(value) {
    this.choiceService = {
      ...value,
      quantity: 1,
      note: '',
      price_origin: value.price,
      price: value.price,
    };
    
    if (value.type !== VhType.SERVICETIME) {
      this.showOptions = true;
    }
    else {
      this.closeSearchServiceList.emit(this.choiceService);
    }
  }

  chooseService() {
    this.showOptions = false;
    if (this.choiceService['quantity']) {
      this.closeSearchServiceList.emit(this.choiceService);
    }
  }

  changeTypeGood(event) {
    this.indexTab = event;
    if (event == 0) {
      this.serviceList = this.vhQueryCafe
        .getlocalProducts(VhType.SERVICE_SERVICETIME)
        .filter((item) => !item.delete_hidden)
        .map((e) => {
          return { ...e, ...e.units[0] };
        });
    } else {
      this.serviceList = this.vhQueryCafe
        .getlocalProducts(VhType.SERVICE_SERVICETIME)
        .filter(
          (item) =>
            item.id_category == this.listTabs[event]._id && !item.delete_hidden
        )
        .map((e) => {
          return { ...e, ...e.units[0] };
        });
    }
    this.searchService(this.tempVal);
  }

  selectFoodsCancel() {
    this.showOptions = false;
  }

  /**Thm cleavejs chnh sa gi mn n */
  updatePrice(item) {
    let price = this.editPrice['price-' + item._id].getRawValue();
    this.choiceService.price = price ? parseFloat(price) : 0;
    this.choiceService.quantity =
      this.choiceService.quantity > 0 ? this.choiceService.quantity : 1;
  }

  /** Tng s lng sn phm */
  increaseQuantity(item) {
    if (item.quantity != '') item.quantity = parseFloat(item.quantity);
    item.quantity = item.quantity + 1;
  }
  /** Gim s lng sn phm */
  reduceQuantity(item) {
    if (item.quantity != '') item.quantity = parseFloat(item.quantity);
    item.quantity = item.quantity - 1;
  }
}
